@import url(https://fonts.googleapis.com/css2?family=Arvo:ital@1&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.logo{
    text-align: center;
    margin: 0 auto;
    height: 100px;
    color: white;
    font-size: 60px;
    font-family: 'Bebas Neue', cursive;
    letter-spacing: 0em;
    text-decoration: none;
    text-shadow: 2px 3px rgba(0, 0, 0, 0.651);
}
.items-container{
    display: block;
    height: 200vh;
    width: 20%;
    background-color: #2A3F54;
    border-right: 1px solid black;
}
.objects{
    width: 100%;
    list-style: none;
    padding-top: 10px;
}
li{
    padding-top: 8px;
    padding-left: 10px;
}
li div{
    font-family: 'Arvo', serif;
    font-size: 30px;
    color: white;
    letter-spacing: 2px;
    padding-left: 3px;
}

.price-chart{
    max-width: 1200px;
    height: 400px;
    margin-left: 50px;
    margin-bottom: 50px;
    margin-top: 50px;
}
.leftQuantity-chart{
    max-width: 1200px;
    height: 400px;
    margin-left: 50px;
    margin-bottom: 50px;
}
.averageSellPrice-chart{
    max-width: 1200px;
    height: 400px;
    margin-left: 50px;
    margin-bottom: 50px;
}
.soldAmount-chart{
    max-width: 1200px;
    height: 400px;
    margin-left: 50px;
    margin-bottom: 50px;
}
.left-item-information{
    padding-right: 50px;
    padding-left: 20px;
}
a{
    text-decoration: none;
    color: black;
    font-weight: bold;
}
a:hover{
    color: cadetblue;
}
.itemInformation-container{
    display: block;
    height: 200vh;
    width: 79.5%;
    background-color:#EDEDED;
    border-left: 1px solid black;
}
.body-container{
    display: -webkit-flex;
    display: flex;
}
@media only screen and (max-width: 1286px) {
    .body-container {
      width: 100%;
    }
  }
